define([], function() { return "\r\n<div id=\"Login\">\r\n  <form method=\"post\" id=\"LoginForm\">\r\n    <div>\r\n      <label for=\"LoginUsername\">Username</label>\r\n      <input type=\"text\" name=\"LoginUsername\" id=\"LoginUsername\" value=\"\" placeholder=\"Username\" />\r\n    </div>\r\n    <div>\r\n      <label for=\"LoginPassword\">Password</label>\r\n      <input type=\"password\" name=\"LoginPassword\" id=\"LoginPassword\" value=\"\" placeholder=\"Password\" />\r\n    </div>\r\n    <div class=\"Submit\">\r\n      <input type=\"submit\" name=\"LoginSubmit\" id=\"LoginSubmit\" value=\"Login\" />\r\n    </div>\r\n  </form>\r\n</div>\r\n\r\n<style type=\"text/css\">\r\n\r\n#Login {\r\n  background : #fff;\r\n  width : 400px;\r\n  height : 230px;\r\n  padding : 20px;\r\n  position : absolute;\r\n  left : 50%;\r\n  top : 50%;\r\n  z-index : 9999;\r\n  margin-left : -200px;\r\n  margin-top : -155px;\r\n  border : 4px solid rgba(0, 0, 0, .1);\r\n  -webkit-box-shadow: 0 0 5px 4px rgba(0,0,0,0.15);\r\n  -moz-box-shadow: 0 0 5px 4px rgba(0,0,0,0.15);\r\n  box-shadow: 0 0 5px 4px rgba(0,0,0,0.15);\r\n  box-sizing : border-box;\r\n\r\n  display : none;\r\n}\r\n  #LoginForm > div {\r\n    margin-bottom : 20px;\r\n  }\r\n  #Login label {\r\n    display : block;\r\n    font-weight : bold;\r\n    height : 30px;\r\n    line-height : 30px;\r\n  }\r\n  #Login input[type=text],\r\n  #Login input[type=password] {\r\n    display : block;\r\n    width : 100%;\r\n    height : 24px;\r\n    padding : 4px;\r\n    line-height : 12px;\r\n    box-sizing : border-box;\r\n  }\r\n  #Login input[type=submit] {\r\n    height : 40px;\r\n    padding : 10px;\r\n    padding-left : 20px;\r\n    padding-right : 20px;\r\n    box-sizing : border-box;\r\n    background : rgba(0, 0, 0, .1);\r\n    border : 0 none;\r\n    cursor : pointer;\r\n  }\r\n  #Login .Submit {\r\n    text-align : right;\r\n  }\r\n\r\n</style>\r\n\r\n"; });